.majorDiv {
  /* border: 5px solid black; */
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  padding-top: 8%;
}

h1 {
  font-size: 100px;
  text-align: center;
}

button {
  outline: none; /* Gets rid of that default blue highlight border on buttons */
  display: block;
  margin: 0 auto;
  height: 100px;
  width: 250px;
  border-width: 10px;
  font-size: 40px;
  border-radius: 20px;
}



